.nav {
  display: flex;
  justify-content: space-between;
  background-color: white;
  padding: 10px 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
}

.nav a {
  text-decoration: none;
  color: #2f2f2f;
  font-size: 12px;
  text-align: center;
  font-weight: 600;
  width: 60px;
}